        width: 50%;
        height: 190px;

        &.out
        {
            object-fit: cover;
            object-position: center;
            right: 0!important;
        }
    }

    @include mq('phablet', max)
    {
        &-card-body
        {
            width: 100%;
        }

        img
        {
            position: absolute;
            top: 15px;
            right: 15px;
            height: calc(100% - 30px);
