}

.stock
{
    .row
    {
        .col-12
        {
            display: flex;
            padding: 20px;
            border-radius: 6px;
            box-shadow: 0px 0px 35px rgba(0, 0, 0, 0.08);

            img
            {
                object-fit: cover;
                object-position: center;
                width: 195px;
                border-radius: 6px;
                margin-right: 20px;
            }

            span
            {
                font-size: 1.6em;
                display: block;
                margin-bottom: 10px;
                color: $light-muted;
            }

            div a
            {
                display: block;
                font-size: 1.8em;
                margin-bottom: 10px;
            }

            p
            {
                font-weight: 300;
                margin-bottom: 0;
                color: $light-muted;
                
                @include textClamp(3);
            }

            &:first-child
            {
                margin-top: 0!important;
            }

            @include mq('tablet', max)
            {
                flex-direction: column;
                text-align: center;

                img
                {
                    margin-bottom: 15px;
                }
            }
        }
    }
}